@import "../assets/styles/app";

.app {
  position: relative;
  overflow: hidden;
  background: #FFF;

  .imgWrapper {
    width: 100%;
    position: relative;
    padding-top: vwM(12);

    @include desktop {
      padding-top: vwD(48);
    }

    .back {
      display: block;
      width: 100%;
      height: auto;
    }

    .woods1 {
      position: absolute;
      display: block;
      width: vwD(810);
      height: auto;
      top: vwD(-61);
      left: vwD(-73);
    }

    .woods2 {
      position: absolute;
      display: block;
      width: vwD(810);
      height: auto;
      top: vwD(445);
      left: vwD(1413);
    }

    .woods3 {
      position: absolute;
      display: block;
      width: vwD(810);
      height: auto;
      top: vwD(1627);
      left: vwD(70);
    }

    .headerLinks {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: vwD(66);
      left: vwD(1613);

      a {
        display: block;

        &:not(:first-child) {
          margin-top: vwD(27);
        }

        img {
          display: block;
          width: vwD(308);
          height: auto;
        }
      }
    }

    .address {
      @include centerRow;
      position: absolute;
      top: vwM(192);
      left: vwM(14);
      background: #00ff38;
      border: vwM(2) solid #000;
      border-radius: vwM(17);
      width: vwM(317);
      height: vwM(21);

      @include desktop {
        border: vwD(4) solid #000;
        border-radius: vwD(17);
        width: vwD(1008);
        height: vwD(61);
        left: vwD(485);
        top: vwD(875);
      }

      p {
        @include DigitalStrip2BB_Bold;
        font-size: vwM(10);
        letter-spacing: 0.06em;
        color: #171717;

        @include desktop {
          font-size: vwD(36);
        }
      }
    }

    .telegram_red {
      display: block;
      position: absolute;
      top: vwD(1400);
      left: vwD(546);

      img {
        display: block;
        width: vwD(411);
        height: auto;
      }
    }

    .dextools {
      display: block;
      position: absolute;
      top: vwD(1554);
      left: vwD(1380);

      img {
        display: block;
        width: auto;
        height: vwD(117);
      }
    }

    .howTo_m {
      @include onlyMobile();
      position: absolute;
      width: vwM(179);
      height: auto;
      left: vwM(241);
      top: vwM(268);
    }

    .howTo_d {
      @include onlyDesktop();
      position: absolute;
      width: vwD(751);
      height: auto;
      left: vwD(1141);
      top: vwD(1184);
    }

    .about_m {
      @include onlyMobile();
      position: absolute;
      width: vwM(145);
      height: auto;
      left: vwM(268);
      top: vwM(421);
    }

    .about_d {
      @include onlyDesktop();
      position: absolute;
      width: vwD(592);
      height: auto;
      left: vwD(1272);
      top: vwD(1907);
    }

    .linksBottom {
      display: flex;
      position: absolute;
      left: vwM(145);
      top: vwM(596);

      @include desktop {
        left: vwD(665);
        top: vwD(2657);
      }

      a {
        display: block;

        &:not(:first-child) {
          margin-left: vwM(11);

          @include desktop {
            margin-left: vwD(47);
          }
        }

        img {
          display: block;
          width: vwM(67);
          height: auto;

          @include desktop {
            width: vwD(308);
          }
        }
      }
    }

  }

  .addressWrapper {
    @include centerRow;
    margin: vwM(14) auto 0;
    background: #00ff38;
    border: vwM(2) solid #000;
    border-radius: vwM(17);
    width: vwM(317);
    height: vwM(21);

    @include desktop {
      margin: vwD(36) auto 0;
      border: vwD(4) solid #000;
      border-radius: vwD(17);
      width: vwD(1008);
      height: vwD(61);
      left: vwD(485);
      top: vwD(875);
    }

    p {
      @include DigitalStrip2BB_Bold;
      font-size: vwM(10);
      letter-spacing: 0.06em;
      color: #171717;

      @include desktop {
        font-size: vwD(36);
      }
    }
  }

  .mail {
    @include DigitalStrip2BB_Bold;
    font-size: vwM(12);
    line-height: 275%;
    letter-spacing: 0.04em;
    color: #000;
    text-align: center;

    @include desktop {
      font-size: vwD(29);
    }
  }


}